*{
  margin: 0;

}
.App{
  background-color: #6200EE;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  color: #6200EE;
  font-weight: bold;
  
}
textarea {
  font-size: 1rem;
  font-weight: bold;
}
h2 {
  color: #3700B3;
  font-size: 1.5rem;
  padding: 2px 0px 5px 0px;
  text-align: center;
}
.Container{
background-color: #ffffff;
font-size: 1rem;
width: auto;
height: 40vh;
display: flex !important;
flex-flow: column;
padding: 0px 12px 5px 12px;
margin: 60px 0px 0px 0px;
border-radius: 2px;
box-shadow: 0 2px 1px rgba(0,0,0,0.09), 
            0 4px 2px rgba(0,0,0,0.09), 
            0 8px 4px rgba(0,0,0,0.09), 
            0 16px 8px rgba(22, 10, 10, 0.09),
            0 32px 16px rgba(0,0,0,0.09);
}
 input:focus,
 textarea:focus {
  outline-color: #3700B3;
 }


 @media only screen and (min-width: 992px) {
    .App{
  background-color: #6200EE;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  color: #6200EE;
  font-weight: bold;
  
}
textarea {
  font-size: 2rem;
  font-weight: bold;
}
h2 {
  color: #3700B3;
  font-size: 3rem;
  padding: 10px 0px 50px 0px;
  text-align: center;
}
.Container{
background-color: #ffffff;
font-size: 2rem;
width: auto;
height: 60vh;
display: flex !important;
flex-flow: column;
padding: 0px 5px 50px 5px;
margin: 60px 0px 0px 0px;
border-radius: 2px;
box-shadow: 0 2px 1px rgba(0,0,0,0.09), 
            0 4px 2px rgba(0,0,0,0.09), 
            0 8px 4px rgba(0,0,0,0.09), 
            0 16px 8px rgba(22, 10, 10, 0.09),
            0 32px 16px rgba(0,0,0,0.09);
}
 input:focus,
 textarea:focus {
  outline-color: #3700B3;
 }
 }

